import React from 'react';

import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';

export default function LocationFieldControl({
  fieldName,
  label,
  disabled,
  formik,
}: {
  readonly fieldName: string;
  readonly label: [string, string];
  readonly disabled?: boolean;
  readonly formik: any;
}) {
  const lat = (
    <Box key={`${fieldName}longitude`} sx={{ height: '75px' }}>
      <TextField
        fullWidth
        size="small"
        disabled={disabled}
        id={`${fieldName}longitude`}
        name={`${fieldName}[0]`}
        label={`${label[0]}`}
        value={
          isNaN(formik.values[fieldName][0]) ? '' : formik.values[fieldName][0]
        }
        onChange={formik.handleChange}
        onBlur={formik.handleBlur}
        error={
          formik.touched[fieldName] &&
          Boolean(formik.errors[fieldName] && formik.errors[fieldName][0])
        }
        helperText={
          formik.touched[fieldName] &&
          formik.errors[fieldName] &&
          formik.errors[fieldName][0]
        }
      />
    </Box>
  );
  const lng = (
    <Box key={`${fieldName}longitude`} sx={{ height: '75px' }}>
      <TextField
        fullWidth
        size="small"
        disabled={disabled}
        id={`${fieldName}longitude`}
        name={`${fieldName}[1]`}
        label={`${label[1]}`}
        value={
          isNaN(formik.values[fieldName][1]) ? '' : formik.values[fieldName][1]
        }
        onChange={formik.handleChange}
        onBlur={formik.handleBlur}
        error={
          formik.touched[fieldName] &&
          Boolean(formik.errors[fieldName] && formik.errors[fieldName][1])
        }
        helperText={
          formik.touched[fieldName] &&
          formik.errors[fieldName] &&
          formik.errors[fieldName][1]
        }
      />
    </Box>
  );
  return (
    <>
      <Box key="left" sx={{ p: 1, width: '50%' }}>
        {lat}
      </Box>
      <Box key="right" sx={{ p: 1, width: '50%' }}>
        {lng}
      </Box>
    </>
  );
}
