import React from 'react';

import Checkbox from '@mui/material/Checkbox';
import ListItemText from '@mui/material/ListItemText';
import Box from '@mui/material/Box';
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import FormHelperText from '@mui/material/FormHelperText';

export default function SelectMultiFieldControl({
  fieldName,
  label,
  items,
  disabled,
  formik,
}: {
  readonly fieldName: string;
  readonly label: string;
  readonly items: readonly string[];
  readonly disabled?: boolean;
  readonly formik: any;
}) {
  return (
    <Box key={fieldName} sx={{ height: '75px' }}>
      <FormControl
        fullWidth
        size="small"
        disabled={disabled}
        error={formik.touched.truckType && Boolean(formik.errors.truckType)}
      >
        <InputLabel id={fieldName}>Truck Type</InputLabel>
        <Select
          labelId={fieldName}
          id={fieldName}
          name={fieldName}
          label={label}
          multiple
          value={formik.values[fieldName]}
          renderValue={(selected) => selected.join(', ')}
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
        >
          {items.map((item) => (
            <MenuItem key={item} value={item}>
              <Checkbox checked={formik.values[fieldName].indexOf(item) > -1} />
              <ListItemText primary={item} />
            </MenuItem>
          ))}
        </Select>
        <FormHelperText>
          {formik.touched[fieldName] && formik.errors[fieldName]}
        </FormHelperText>
      </FormControl>
    </Box>
  );
}
